import assert from 'assert'
import { NewFireAlarm } from '../src/NewAlarm'
import { FakeFireRoom as Room } from '../test/FakeRoom'

suite('when alarm with empty battery', function () {
    var alarm;
    setup(function() {
        alarm = new NewFireAlarm()
            .withMaxTemperatureCondition(40)
            .withSmokeReaction(true)
            .withEmptyBattery()
            .build();
    });

    //хороший тест: AAA (arrange act assert)
    test('If Fire Alarm is not signaling when fire is in the room', function () {
        //arrange
        //использование фейковой комнаты и  dsl
        var room = new Room()
                        .withFire();
        //act

        //
        assert.equal(false, alarm.isSignalOn(room));
    });

    test('alarm is not signaling when there is no fire in the room', function() {
        var room = new Room();

        assert.equal(false, alarm.isSignalOn(room));
    });
});